<template>
	<div id="app">
		<div class="progressImg">
			<img src="@/assets/ethLogo.png" />
			<img src="@/assets/dtLogo.png" />
			<img class="data" src="@/assets/data.png" />
		</div>
		<div class="progressBox">
			<span>正在迁移区块数据</span>
			<div class="progressBoxCenter">
				<div>
					<div :style="{width: width+'%'}">
						<div>{{width}}%</div>
					</div>
				</div>
			</div>
			<span>预计剩余时间{{hour}}小时</span>
		</div>
	</div>
</template>

<script>
export default {
	name: 'app',
	data(){
		return {
			width: 0,
			hour: 16,
		}
	},
	mounted(){
		//2020-07-27 18:00
		let stratTime = 1595844000;
		//2020-07-28 10:00
		let endTime = 1595901600;
		
		let chazhi = (endTime - stratTime);
		
		let currentTime = Math.floor(new Date().getTime() / 1000)
		let time = setInterval(()=>{
			if(this.width > 100){return}
			this.width = Math.floor((1 - (endTime - currentTime) / chazhi) * 100)
			this.hour = Math.ceil((endTime - currentTime) / 3600)
		},1000)
	}
}
</script>

<style>
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	width: 100vw;
	height: 100vh;
	background-color: #273446;
	padding: 404px 120px;
}
.progressImg{
	display: flex;
	position: relative;
	justify-content: space-between;
}
.progressImg>img{
	height: 132px;
}
.progressImg>.data{
	width: 28px;
	height: 36px;
	position: absolute;
	left: 96px;
	top: 36px;
	animation: rotating 1.5s linear infinite;
}
@keyframes rotating {
	0%{
		left: 96px;
		top: 25px;
	}
	16%{
		top: 18px;
		left: 140px;
	}
	32%{
		top: 0px;
		left: 184px;
	}
	48%{
		top: -6px;
		left: 228px;
	}
	64%{
		top: 2px;
		left: 272px;
	}
	80%{
		top: 18px;
		left: 316px;
	}
	100% {
		left: 360px;
		top: 36px;
	}
}
.progressBox{
	font-size: 24px;
	color: #fff;
	margin-top: 88px;
}
.progressBoxCenter{
	width: 100%;
}
.progressBoxCenter>div{
	width: 100%;
	height: 16px;
	background-color: #E3E3E3;
	border-radius: 8px;
	position: relative;
	margin: 20px 0 110px;
}
.progressBoxCenter>div>div{
	position: absolute;
	width: 10%;
	height: 100%;
	background-color: #21C17A;
	border-radius: 8px;
}
.progressBoxCenter>div>div>div{
	position: absolute;
	font-size: 24px;
	bottom: -50px;
	left: 0;
}
</style>
